.skill-container {
    visibility: hidden;
    margin-left: 4rem;
    position: fixed;
    width: 100%;
    height: 100vh;
    background:#181818;
}

.skill-wrapper {
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 100%;
}

.skill-text {
    width: 50%;
    display: flex;
    flex-direction: column;
    margin: auto 4rem auto 4rem;
    color: #fff;
    font-weight: 300;
}

.skill-text .text-header {
    color: #00cce7;
    font-size: 4rem;
    font-weight: 300;
    margin-bottom: 0;
}

.skill-icon-container{
    width: 50%;
    display: flex;
    height: 100vh;
    align-items: center;
    flex-direction: column;
    justify-content: center;

}

.icon-wrapper {
    margin-top: 50px;
    margin-right: 75px;
    width: 100%;
}

.icon-cube {
    display: inline-block;
    width: 125px;
    height: 125px;
    background-color: rgb(104, 104, 104);
    margin: 5px;
}

.icon {
    font-size: 5rem;
    padding: 24px;
    
}


.icon-cube:hover {
    background:#00cce7;
    
}


@media only screen and (max-width: 768px) {

    .skill-container {
        margin-left: 0;
        margin-top: 4rem;
    }

    .skill-text .text-header {
        margin-top: 0;
    }


}

@media only screen and (max-width: 680px) {
    
    .skill-wrapper {
        flex-direction: column;
    }

    .skill-text {
        width: 80%;
        text-align: center;
        height: 50%;
        margin-top: 50px;
    }

    .skill-icon-container {
        width: 100%;
        flex-direction: row;
        text-align: center;
    }

    .icon-wrapper {
        margin: 0 0 5rem 0;
    }

    
}

@media only screen and (max-width: 550px) {
    .icon-cube {
        width: 110px;
        height: 110px;
    }

    .icon {
        font-size: 3rem;
        
    }

    
}

@media only screen and (max-width: 450px) {

    

    .icon-cube {
        width: 100px;
        height: 100px;
    }
}