.contact-container {
    margin-left: 4rem;
    position: fixed;
    visibility: hidden;
    width: 100%;
    background:#181818;
    height: 100vh;
}

.contact-wrapper {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    height: 100%;
    
}

.form-container {
    height: 100vh;
    background-color: #333;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 40%;
    justify-content: center;
}

.form-header {
    color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-bottom: 30px;
    
}

.form-header > h2 {
    margin: 0 0 10px 0;
    font-weight: 300;
    color:  #00cce7;
    font-size: 3rem;
}

.form-header > h5 {
    margin: 0 0 5px 0;
    font-weight: 100;
}

.input-wrap {
    width: 80%;
    margin: 0 0 25px 0;
}

.form-control {
    width: 100%;
    border: none;
    background-color: rgb(85, 85, 85);
    padding: 0 0 0 5px;
    color: #fff;
    font-size: 0.8rem;
    font-weight: 100;
    
}

.input-wrap {
    display: flex;
    justify-content: center;
    align-items: center;
}

.input-control {
    height: 40px;
}

.btn-submit {
    color: #fff;
    border: solid 2px var(--btn-blue);
    background-color: var(--btn-blue);
    border-radius: 5px;
    padding: 10px 25px;
    font-size: .8rem;
    cursor: pointer;
    transition: .15s;
    font-weight: 300;
}

.btn-submit:hover {
    background-color: #00b7cf;
    border: solid 2px #00b7cf;
    color: #333;
}

.svg-container {
    width: 60%;
    height: 100%;
    background-color: rgb(32, 32, 32);
    display: flex;
    align-items: center;
    justify-content: center;
}

.form-image {
    width: 80%;
    margin-right: 4rem;
}


@media only screen and (max-width: 768px) {
    .contact-container {
        margin-top: 4rem;
        margin-left: 0;
    }

    .contact-wrapper {
        flex-direction: column;
    }
    
    .svg-container {
        display: none;
    }

    .form-container {
        width: 100vh;
        margin: 0;
        height: 100vh;
        padding: 0;
        justify-content: center;
        align-items: center;
    }

    .input-wrap {
        width: 60%;
    }
}

@media only screen and (max-width: 600px) {
    .input-wrap {
        width: 40%;
    }
}

@media only screen and (max-width: 400px) {
    .input-wrap {
        width: 30%;
    }
}