.hero-container {
    margin-left: 4rem;
    position: fixed;
    visibility: hidden;
    width: 100%;
    background:#181818;
    height: 100vh;
    
}


.heading-container {
    display: flex;
    height: 100%;
    width: 100%;


}

.heading {
    width: 30%;
    display: flex;
    flex-direction: column;
    margin: auto 4rem auto 4rem;
}

.heading .heading-text {
    padding: 0;
    margin: 0;
    font-size: 5rem;
    line-height: 0.8;
    color: #fff;
    font-weight: 100;

}

.heading .heading-desc {
    color: #fff;
    font-size: .8rem;
}

.heading .heading-text .hi-lite {
    color: #00cce7;
}

.logo-container {
    position: absolute;
    left: 625px;
    top: 75px;
}

.logo-img {
    width: 500px;
}

.svg-hero {
    width: 70%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.svg-hero > svg {
    width: 100%;
}

.btn-contact {
    margin-right: 25px;
    margin-bottom: 15px;
}

@media only screen and (max-width: 1200px) {

    .svg-hero {
        width: 50%;
    }

}

@media only screen and (max-width: 1000px) {

    .heading .heading-text {
        font-size: 3.5rem;
    }
}


@media only screen and (max-width: 768px) {
    .hero-container {
        margin-left: 0;
        margin-top: 4rem;
    }
    .heading .heading-text {
        font-size: 2.5rem;
    }

    .svg-hero {
        margin-right: 0;
    }


}

@media only screen and (max-width: 600px) {
    .svg-hero {
        display: none;
    }

    .heading {
        width: 100%;
    }

    .heading .heading-text {
        font-size: 4.5rem;
    }


}


