.about-container {
    visibility: hidden;
    margin-left: 4rem;
    position: fixed;
    width: 100%;
    background:#181818;
    height: 100vh;
    
}

.about-heading {
    display: flex;
    width: 100%;
    height: 100%;
}

.about-text-container {
    width: 50%;
    display: flex;
    flex-direction: column;
    margin: auto 4rem auto 4rem;
}


.about-text-container > h1 {
    color: #00cce7;
    font-size: 4rem;
    font-weight: 300;
    margin-bottom: 0;
}

.about-text-container > .btn-dark {
    margin: 0;
}

.about-text {
    color: #fff;
    font-weight: 300;
}

.grad-container {
    display: flex;
    height: 100vh;
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;

}

.grad-img {
    width: 300px;
    margin-right: 5rem; 
}

@media only screen and (max-width: 1050px) {
    .grad-img {
        margin-right: 7rem;
    }

    .about-text-container {
    }
}

@media only screen and (max-width: 768px) {
    .about-container {
        margin-left: 0;
        margin-top: 4rem;
    }

    .about-heading {
        flex-direction: column-reverse;
        align-items: center;
        justify-content: center;
        height: 100vh;
    }

    .grad-img {
        margin: 2rem 0 0 0;
        width: 250px;
    }
    .grad-container {
        justify-content: center;
        height: 50vh;
        width: 100%;
    }

    .about-text-container {
        width: 100%;
        height: 50%;
        align-items: center;
        text-align: center;
        padding-bottom: 4rem;
    }

    .about-text {
        margin: 5px 4rem 5px 4rem;
    }

    .btn-container {
        margin-top: 25px;
        margin-bottom: 25px;
    }

    .about-text-container > h1 {
        margin-top: 25px;
    }


    
}

@media only screen and (max-width: 550px) {
    .grad-img {
        width: 225px;
        margin-top: 2rem;
    }

    .about-text-container {
        padding-bottom: 6rem;
    }

}

@media only screen and (max-width: 450px) {
    .about-text-container > h1 {
        font-size: 3rem;
    }

    .about-text-container > p {
        font-size: 0.9rem;
    }
}
